@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap');

div.MovieDetail {
	font-family: 'Source Sans Pro', sans-serif;
	color: #fff;
}

.bg-image {
	height: 100vh;
	filter: blur(5px);
	background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.content {
	position: absolute;
	top: 0;
	width: 80%;
	margin: 100px 10% 0 10%;
	display: grid;
	grid-template-columns: 250px 1fr;
}

.content img {
	width: 250px;
	border-radius: 0;
}

.content h2 {
	color: #fff;
	font-size: 25px;
}

.movie-info {
	background-color: rgba(0,0,0, 0.4);
	padding: 0 30px;
}

.movie-info div {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.content ul {
	justify-content: center;
}

.content ul li {
	margin: 0 10px;
}

.content button {
	position: static;
	margin-right: 10px;
}

button:focus {
	outline: 0;
}

@media (max-width: 768px) {
	.bg-image {
		background-attachment: fixed;
	}

	.content {
		width: 100%;
		margin: 0;
		grid-template-columns: 1fr;
	}

	.content img {
		width: 100%;
		order: 1;
	}

	.movie-info div {
		display: grid;
		grid-template-columns: 1fr;
	}

	.content ul {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		margin: 0;
	}

	.content ul li {
		margin-left: 0;
	}

	button {
		margin-bottom: 20px;
	}
}
