form {
	position: relative;
}

input {
	width: 300px;
	height: 30px;
	border-radius: 20px;
	border: 0;
	padding-left: 10px;
}

input:focus {
	outline: 0;
}

button {
	width: 80px;
	height: 32px;
	border-radius: 20px;
	border: 0;
	background: linear-gradient(to right, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%);
	color: #fff;
	position: absolute;
	top: 0;
	right: 0;
}

button:hover {
	cursor: pointer;
}

@media (max-width: 768px) {
	input {
		width: 90%;
		margin-bottom: 10px;
	}
}