@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
form {
	position: relative;
}

input {
	width: 300px;
	height: 30px;
	border-radius: 20px;
	border: 0;
	padding-left: 10px;
}

input:focus {
	outline: 0;
}

button {
	width: 80px;
	height: 32px;
	border-radius: 20px;
	border: 0;
	background: linear-gradient(to right, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%);
	color: #fff;
	position: absolute;
	top: 0;
	right: 0;
}

button:hover {
	cursor: pointer;
}

@media (max-width: 768px) {
	input {
		width: 90%;
		margin-bottom: 10px;
	}
}
nav {
	background: rgb(3,37,65);
	color: #fff;
	font-family: 'Source Sans Pro', sans-serif;
}

ul {
	display: flex;
	justify-content: space-between;
	align-items: center;
	list-style-type: none;
	width: 80%;
	margin: auto;
	padding: 0;
}

ul div {
	display: flex;
	align-items: center;
}

ul li {
	cursor: pointer;
	margin: 0;
}

ul div li {
	margin-right: 20px;
}

@media (max-width: 768px) {
	ul {
		display: grid;
		grid-template-columns: 1fr;
	}
}
img {
	width: 100%;
	border-radius: 7px;
}

h2 {
	color: #000;
	font-size: 18px;
}

a {
	font-family: 'Source Sans Pro', sans-serif;
	text-decoration: none;
}
div.Movies {
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	grid-gap: 30px;
	gap: 30px;
	width: 80%;
	margin: auto;
	padding: 100px 0;
}

@media (max-width: 768px) {
	div.Movies {
		grid-template-columns: repeat(2, 1fr);
	}
}
body {
  margin: 0;
}

.App {
  min-height: 100vh;
  position: relative;
}

footer {
  background: rgb(3,37,65);
  color: #fff;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  width: 100%;
}

footer p {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
}

footer img {
  width: 150px;
  margin: 0 10px;
  border-radius: 0;
}

footer a {
  color: rgba(1,180,228, 1);
  font-family: Times, "Times New Roman", serif;
}

footer a:nth-child(2) {
  margin: 7px;
}

footer a:nth-child(2):hover {
  color: #fff;
}
div.MovieDetail {
	font-family: 'Source Sans Pro', sans-serif;
	color: #fff;
}

.bg-image {
	height: 100vh;
	-webkit-filter: blur(5px);
	        filter: blur(5px);
	background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.content {
	position: absolute;
	top: 0;
	width: 80%;
	margin: 100px 10% 0 10%;
	display: grid;
	grid-template-columns: 250px 1fr;
}

.content img {
	width: 250px;
	border-radius: 0;
}

.content h2 {
	color: #fff;
	font-size: 25px;
}

.movie-info {
	background-color: rgba(0,0,0, 0.4);
	padding: 0 30px;
}

.movie-info div {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.content ul {
	justify-content: center;
}

.content ul li {
	margin: 0 10px;
}

.content button {
	position: static;
	margin-right: 10px;
}

button:focus {
	outline: 0;
}

@media (max-width: 768px) {
	.bg-image {
		background-attachment: fixed;
	}

	.content {
		width: 100%;
		margin: 0;
		grid-template-columns: 1fr;
	}

	.content img {
		width: 100%;
		order: 1;
	}

	.movie-info div {
		display: grid;
		grid-template-columns: 1fr;
	}

	.content ul {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		margin: 0;
	}

	.content ul li {
		margin-left: 0;
	}

	button {
		margin-bottom: 20px;
	}
}

